import React, { Component } from 'react'
import img1 from '../img/unsplash_qfn7Ipr6IuI.png'
import img2 from '../img/CaretDown.svg'

export class Project extends Component {
  render() {
    return (
      <>
        <section className="project">
            <div className="container">
                <div className="project__wrapper">
                    <img src={img1} alt=""/>
                    <div className="project__block"></div>
                    <div className="project__content">
                        <h2 className="project__title">Turnkey creation of IT infrastructure for the new terminal at Sochi International Airport</h2>
                        <p className="section__desc project__desc">During the implementation of the project, maximum opportunities are provided for scaling the implemented IT systems, which will allow the airport to effectively develop the infrastructure</p>
                        <a href="#" className="link link__project">Other projects<img src={img2} alt=""/></a>
                    </div>
                </div>
            </div>
        </section>
      </>
    )
  }
}

export default Project