import React, { Component } from 'react'
import arrow from '../img/ArrowDown.svg'
import logoicon1 from '../img/logo-ibm.svg'
import logoicon2 from '../img/image 19.svg'
import logoicon3 from '../img/image 20.svg'
import logoicon4 from '../img/image 21.svg'
import logoicon5 from '../img/logo-commscope.svg'
import logoicon6 from '../img/image 25.svg'

export class Brands extends Component {
  render() {
    return (
      <>
        <section className="brands">
            <div className="container">
                <div className="brands__wraper">
                    <header className="brands__header">
                        <h2 className="brands__title">they trusted us</h2>
                        <a href="#" className="link link__brands">More details <img src={arrow} alt=""/></a>
                    </header>
                    <div className="brands__zone">
                        <div className="brands__block">
                            <img src={logoicon1} alt=""/>
                        </div>
                        <div className="brands__block">
                            <img src={logoicon2} alt=""/>
                        </div>
                        <div className="brands__block">
                            <img src={logoicon3} alt=""/>
                        </div>
                        <div className="brands__block">
                            <img src={logoicon4} alt=""/>
                        </div>
                        <div className="brands__block">
                            <img src={logoicon5} alt=""/>
                        </div>
                        <div className="brands__block">
                            <img src={logoicon6} alt=""/>
                        </div>
                    </div>
                </div>
            </div>
        </section>
      </>
    )
  }
}

export default Brands