import React, { Component } from 'react'
import backPhoto from '../img/ph.png'

export class Call extends Component {
  render() {
    return (
      <>
        <section class="call">
            <div class="container">
                <div class="call__wrapper">
                    <img src={backPhoto} alt="" />
                    <div class="call__content">
                        <h2 class="call__title">Request a call</h2>
                        <p class="call__desc section__desc">Just enter your mobile phone number here and we will call you back. You can ask all the questions that interest you, or place an order</p>
                        <form action="" class="form__call">
                            <input type="tel" class="input" placeholder="+38 (066) 34-29-857" />
                            <button class="btn">Request a call</button>
                        </form>
                    </div>
                </div>
            </div>
        </section>   
      </>
    )
  }
}

export default Call