import React, { Component } from 'react'
import caretDown from '../img/CaretDown.svg'
import face from '../img/face.svg'


export class Ai extends Component {
  render() {
    return (
      <>
        <section class="ai">
            <div class="container">
                <div class="ai__wrapper">
                    <div class="ai__content">
                        <header class="ai__header">
                            <h2 class="ai__title">Virtual Consultant</h2>
                            <p class="ai__desc">Automate the processing of routine messages in the contact center</p>
                            <a href="#" class="link">See details <img src={caretDown} alt="" /></a>
                        </header>
                        <footer class="ai__footer">
                            <div class="ai__block"></div>
                            <div class="ai__block"></div>
                            <div class="ai__block"></div>
                        </footer>
                    </div>
                    <img src={face} alt="" />
                </div>
            </div>
        </section>
      </>
    )
  }
}

export default Ai