import React, { Component } from 'react'
import chat from '../img/Chats.svg'
import image from '../img/ph-1.png'


export class Question extends Component {
  render() {
    return (
      <>
       <section class="question">
            <div class="container">
                <div class="question__wrapper">
                    <div class="question__content">
                        <div class="question__header">
                            <img src={chat} alt="" />
                            <div class="question__text">
                                <h2 class="question__title">Have a question?</h2>
                                <p class="section__desc question__desc">You can ask your question to our manager, who will contact you shortly</p>
                            </div>
                        </div>
                        <form action="" class="form question__form">
                            <input type="text" class="input question__input" placeholder="Question" />
                            <input type="text" class="input" placeholder="E-Mail" />
                            <button class="btn btn__contact">Contact us</button>
                        </form>
                    </div>
                    <img src={image} alt="" class="question__img" />
                </div>
            </div>
        </section> 
      </>
    )
  }
}

export default Question