import React, { Component } from 'react'
import img1 from '../img/unsplash_QdAAasrZhdk.png'
import img2 from '../img/unsplash_SRjZtxsK3Os.png'
import img3 from '../img/unsplash_RFDP7_80v5A.png'

export class Hero extends Component {
  render() {
    return (
      <>
        <section className="hero">
            <div className="container">
                <div className="hero__wrapper">
                    <h1 className="hero__title">Leading system integrator of Russia</h1>
                    <p className="hero__desc">In building IT infrastructure, engineering solutions and security systems</p>
                    <div className="hero__blocks">
                        <div className="hero__card">
                            <img src={img1} alt=""/>
                            <h3>Integrator in Russia</h3>
                        </div>
                        <div className="hero__card">
                            <img src={img2} alt=""/>
                            <h3>Security</h3>
                        </div>
                        <div className="hero__card">
                            <img src={img3} alt=""/>
                            <h3>Outsourcing</h3>
                        </div>
                        
                    </div>

                    <div behavior="" direction="" className="hero__line">
                        <span>Management Consulting</span>
                        <span>Business Applications</span>
                        <span>ITSM</span>
                        <span>Proprietary Products</span>
                        <span>IT infrastructure</span>
                        <span>Engineering systems</span>
                        <span>Business Applications</span>
                        <span>ITSM</span>
                        <span>Proprietary Products</span>
                        <span>IT infrastructure</span>
                        <span>Engineering systems</span>
                    </div>
                </div>
            </div>
        </section>
      </>
    )
  }
}

export default Hero