import React, { Component } from 'react'
import logo from '../img/logo.svg'
import glass from '../img/MagnifyingGlass.svg'
import user from '../img/User.svg'

export class Header extends Component {
  render() {
    return (
      <>
        <header className="header">
        <div className="container">
            <div className="header__wrapper">
                <div className="logo">
                    <img src={logo} alt="" />
                </div>
                <div className="header__menu">
                    <form action="" className="form form__search">
                        <input type="text" name="search" id="input__search" placeholder="Search" className="header__search" />
                        <button className="btn__search">
                            <img src={glass} alt="" />

                            </button>
                    </form>
                    <a href="#" className="link link__user">
                        <img src={user} alt="icon: user" />
                    </a>
                    <div className="header__burger">
                        <span></span>
                    </div>

                </div>
            </div>
        </div>
    </header>
      </>
    )
  }
}

export default Header